import React from 'react';
import s from './../Dialogs.module.css'
import { NavLink } from 'react-router-dom';

const DialogItem = (props) => {
  return (
    <div className={s.diolog}>
      <img src="https://static.vecteezy.com/system/resources/previews/002/592/172/original/smile-emoji-pop-art-line-style-icon-free-vector.jpg"/>
      <NavLink to={'/dialogs/' + props.id} className = { navData => navData.isActive ? s.active : s.item }>{props.name} </NavLink>
    </div>
  )
}



export default DialogItem